.detail-page {
    height: 812px;
    width: auto;
    
    overflow: hidden;
    user-select: none;
    width: 100%;
}

header{
    text-align: center;
    
}
header>h2{
font-weight: 700;
font-size: 16px;
line-height: 22px;

color: var(--gray90)


}
.return-btn {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    position: absolute;
    width: 40px;
    height: 50px;
    left: 12px;
    border-radius: 1000px;

}
.step{
    
}

.steps-header {

    display: grid;
    grid-template-columns: 1fr 1fr;
    margin-top: 30px;
    margin-bottom: 17px;
    max-width: 376px;
    width: 90%;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
}

.step-bar {
    position: relative;
    top: -70%;
    z-index: -1;
    max-width: 200px;
    max-width: 191px;
    height: 2px;
    background: #F7F0FE;


}

.step-num {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    margin: auto;
    border-radius: 1000px;
    max-width: 20px;
    height: 20px;
    padding: 5px;
}

.step-content {

    font-family: 'Pretendard';
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    padding: 5px;
    text-align: center;

    color: #333333;

}

.scripts-wrapper {
    overflow-y: scroll;
    width: 100%;
}


.script {
    display: flex;
    max-width: 285px;
    padding: 7px;
}

.bookmark {
    cursor: pointer;
    margin-top: 100%;
}


.video-detail {
    width: 100%;
    max-width: 375px;
    margin-left: auto;
    margin-right: auto;
    height: 812px;
    display: flex;
    flex-direction: column;
}

.video-wrapper {
position: absolute;
left: 0;
}


.video-wrapper>div {
    top: 0;
    left: 0;
    max-width: 100%;
    height: 100%;
}


.script-content {

font-weight: 700;
font-size: 16px;
line-height: 22px;

border-radius: 8px;
padding: 12px;

/* Gray/90 */
color: #1C1C1C;


}

.script-translation {

font-weight: 500;
font-size: 16px;
color: #333333;

}

.detail-type-wrapper {
    height: 640px;
    overflow: hidden;
    margin-bottom: 15px;
}

.script-btn{
    cursor: pointer;
    display: flex;
justify-content: center;
align-items: center;

width:40px;
height: 40px;
background-color: #000000;
opacity: 0.3;
border-radius: 1000px;
margin-left: auto;
margin-right: auto;

}

.script-btn-wrapper{
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    width: 100%;
    max-width: 356px;
}
.script-prev-btn{
}
.script-next-btn{
}

.script-btn>object{
    cursor: pointer;
}
.scripts-wrapper::-webkit-scrollbar {
    max-width: 8px;
    border-radius: 4px;
}

.scripts-wrapper::-webkit-scrollbar-track {
    background: #f0f0f0;
    border-radius: 4px;
}

.scripts-wrapper::-webkit-scrollbar-thumb {
    background: #aaa;
    border-radius: 4px;
}

.scripts-wrapper::-webkit-scrollbar-thumb:hover {
    background: #999;
    border-radius: 4px;
}



.shadowing-bar{
height: 24px;
background: linear-gradient(92.45deg, #D2B1FC -5.06%, #913FF7 104.69%);
border-radius: 8px;

}

.shadowing-guide{
display: flex;
flex-direction: row;
align-items: center;
justify-content: left;
margin-top: 274px;
width: 80%;
max-width: 335px;
height: 42px;
padding-left: 20px;
padding-right: 20px;
background: #F7F7F7;
border-radius: 8px;
margin-left: auto;
margin-right: auto;


font-weight: 700;
font-size: 14px;
color: #1C1C1C;

}

.bar-wrapper{
    
box-sizing: border-box;
width: 100%;
display: block;
margin-bottom: 20px;
margin-top: 272px;
margin-left: auto;
margin-right: auto;
padding: 10px;

max-width: 375px;
height: 44px;

border: 1px solid #E4E4E4;
border-radius: 8px;


}

.video-bottom-nav{
    position: absolute;
    display: flex;
    justify-content: center;
    bottom: 8%;
    width: 90%;
}
object {
    pointer-events: none;
}
svg { cursor: pointer; }